.socials {
	font-size: 0;
	&__link {
		display: inline-block;
		vertical-align: middle;
		@include tr(opacity .3s);
		@include hover {
			opacity: .7; }
		&:not(:last-child) {
			margin-right: 15px; }
		&_fb {
			@include s('fb'); }
		&_tw {
			@include s('tw'); }
		&_vk {
			@include s('vk'); }
		&_inst {
			@include s('inst'); } } }
